#cover:after,
#theend:after {
  opacity: 0;
}
#cover h2,
#theend h2 {
  font-size: 70px;
  margin-bottom: 10px;
  line-height: 1.4;
}
#cover p,
#theend p {
  font-size: 24px;
  line-height: 1.4;
  padding: 10px 25px;
}
.cover img {
  width: auto;
  height: auto;
  min-width: 100%;
  min-height: 100%;
}
.logo {
  width: 75px;
  background-color: #fff;
  padding: 5px;
}
.logo + .logo {
  margin-left: 30px;
}
